/*blocks*/
.menu {
  max-width: 1170px;
  min-height: 61px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 18px 0 19px 0;
  box-sizing: border-box; }
  .menu a {
    font-family: "Poppins", "Arial", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    color: #1D1613; }
    .menu a:hover {
      color: #A87B62; }
  .menu.menu--tablet {
    display: none; }
  @media (max-width: 1023px) {
    .menu.menu--tablet {
      display: flex;
      width: auto;
      height: auto;
      margin: 0;
      padding: 0; }
      .menu.menu--tablet svg {
        stroke: #1D1613;
        fill: none; }
    .menu.menu--desktop {
      display: none; } }

.menu__list {
  display: flex;
  list-style-type: none;
  margin: 0;
  padding: 0; }
  .menu__list li {
    margin: 0 34px 0 0;
    font-family: "Poppins", "Arial", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    color: #1D1613; }
  .menu__list li:last-child {
    margin: 0; }

.page-header {
  width: 100%;
  margin: 0 auto;
  padding: 30px 0 0 0; }
  .page-header a {
    text-decoration: none; }
  @media (max-width: 1023px) {
    .page-header {
      padding: 30px 0; } }
  @media (max-width: 767px) {
    .page-header {
      padding: 20px 0; } }

.page-header__top {
  width: 1170px;
  min-height: 66px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin: 0 auto;
  padding: 0 0 19px 0;
  box-sizing: border-box; }
  @media (max-width: 1239px) {
    .page-header__top {
      width: 970px; } }
  @media (max-width: 1023px) {
    .page-header__top {
      padding: 0;
      width: 100%;
      justify-content: space-between; } }

.page {
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-height: 100%;
  width: 100%;
  margin: 0;
  padding: 0; }

.search {
  min-width: 140px;
  width: max-content;
  border: none;
  border-bottom: solid 1px transparent;
  outline: none;
  background: transparent;
  padding: 0;
  margin: 0 0 0 29px;
  font-family: "Poppins", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: #9B9997; }
  .search:hover {
    border-bottom: solid 1px #9B9997;
    cursor: pointer; }

.wrapper-search {
  position: relative;
  margin: 17px 0 0 0; }
  .wrapper-search label {
    position: absolute;
    top: 4px;
    left: 0; }
    .wrapper-search label:hover {
      cursor: pointer; }
  @media (max-width: 1023px) {
    .wrapper-search {
      display: none; } }

.wrapper-login-busket {
  min-width: 205px;
  display: flex;
  justify-content: flex-end;
  margin: 17px 0 0 0; }
  .wrapper-login-busket a:first-child {
    margin: 0 35px 0; }
  .wrapper-login-busket a {
    font-family: "Poppins", "Arial", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    color: #1D1613; }
    .wrapper-login-busket a:hover {
      color: #A87B62; }
  .wrapper-login-busket.wrapper-login-busket--tablet {
    display: none; }
  @media (max-width: 1023px) {
    .wrapper-login-busket.wrapper-login-busket--tablet {
      display: flex;
      min-width: auto;
      width: auto; }
      .wrapper-login-busket.wrapper-login-busket--tablet svg {
        stroke: #1D1613;
        fill: none; }
      .wrapper-login-busket.wrapper-login-busket--tablet a {
        margin: 0; }
    .wrapper-login-busket.wrapper-login-busket--desktop {
      display: none; } }

.wrapper-page-header-top {
  width: 100%;
  border-bottom: 1px solid #F6F5F4; }
  @media (max-width: 1023px) {
    .wrapper-page-header-top {
      box-sizing: border-box;
      padding: 0 45px;
      border-bottom: none; } }
  @media (max-width: 767px) {
    .wrapper-page-header-top {
      padding: 0 15px; } }
